import React from 'react';
import firebase from 'firebase/app';
import { Link } from 'react-router-dom';
import { DarkBack } from '../Common/DarkBack';
export class WhichQuiz extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            username: "manishekaneja",
            code: "1528223617594"
        }
    }
    render() {
        return (
            <DarkBack>
                <div style={{
                    display: 'flex',
                    flexDirection: 'row',
                    justifyContent: 'center',
                    width: '100%',
                    height: '100%'
                }}>
                    <div style={{ display: 'grid', gridTemplateAreas: "'l1 i1 i1 i1 i1' 'l2 i2 i2 i2 i2' ' b b b b b'", gridGap: '1em', alignSelf: 'center', padding: '0.5em' }}>
                        <label style={{ gridArea: 'l1', fontWeight: '700', fontSize: '1.5em', textAlign: 'left' }}>
                            Quiz Of :
                           </label>
                        <input type="email" style={{ gridArea: 'i1', height: '30px', padding: '3px', boxShadow: '0px 0px 10px red', border: '0px', borderRadius: '0.2em' }} placeholder="Code Here" value={this.state.username} onChange={(e) => {
                            this.setState({ username: e.target.value });
                        }} />
                        <label style={{ gridArea: 'l2', textAlign: 'left', fontWeight: '700', fontSize: '1.5em', }}>
                            Test Code:
                        </label>
                        <input type="password" style={{ gridArea: 'i2', height: '30px', padding: '3px', boxShadow: '0px 0px 10px red', border: '0px', borderRadius: '0.2em' }} placeholder="Key Here" value={this.state.code} onChange={(e) => {
                            this.setState({ code: e.target.value })
                        }} />
                        <Link to='/rules' style={{ gridArea: 'b', width: '100%', height: '100%', padding: '0.5em', fontSize: '1.2em', fontWeight: '700', backgroundColor: 'lightgreen', border: '0px', borderRadius: '0.2em', boxShadow: '0px 0px 2em green' }}
                            onClick={() => {
                                this.props.loadQuiz(this.state)
                                firebase.database().ref('/users/' + this.state.username + "/" + this.state.code).on('value', (snapshot) => {
                                    if (snapshot.val()) {
                                        this.props.take_quiz(snapshot.val());
                                    }
                                    else {
                                        console.log("NOOO")
                                    }
                                });
                            }}>
                            Load
                        </Link>
                    </div>
                </div>
            </DarkBack >
        )
    }
}


