import React from 'react';
import './quizDetails.css';
import firebase from 'firebase/app';
import { Redirect } from 'react-router-dom';
import { QuestionBlock } from './QuestionBlock';

export default class Create extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            ques: this.props.questions[this.props.index],
            flag: false
        }
    }
    render() {
        console.log(this.props.index >= this.props.questions.length );
        if (this.props.index >= this.props.questions.length ) {
            return (
                <Redirect to="/" />
            )
        }
        if (!this.props.quizBlock.details.questionNo || this.props.quizBlock.questionNo === 0) {
            return (
                <Redirect to="/create" />
            )
        }
        return (
            <div className="full create_design" >
                <div>
                    <div className="navi" >
                        <button disabled={this.props.index !== this.props.questions.length - 1} className="submit" style={{ flex: 1, margin: '0em 1em' }} onClick={() => {
                            if (this.props.index <= this.props.questions.length - 1) {
                                if (this.state.ques.questionStatement.trim().length === 0) {
                                    alert('Enter Question Statement is ness');
                                    return;
                                }
                                if (this.state.ques.questionOptions[0].trim().length === 0 && this.state.ques.questionOptions[1].trim().length === 0) {
                                    alert('Enter atleast 2 options');
                                    return;
                                }
                                this.props.updateQuesArray(this.props.questions.map((e) => {
                                    if (e === this.props.questions[this.props.index]) {
                                        return this.state.ques;
                                    }
                                    else {
                                        return e;
                                    }
                                }));
                                firebase.database().ref('users/' + this.props.user + "/" + new Date().getTime()).set(this.props.quizBlock);
                                this.props.inc_index()
                                alert("Submition Done");
                            }
                        }
                        }>
                            Submit
                            </button>
                    </div>
                </div>
                <div style={{ overflow: 'scroll', width: '100%', height: '100%' }}>
                    <QuestionBlock index={this.props.index}
                        ques={this.state.ques}
                        updateQues={(ques) => {
                            this.setState({
                                ques: ques.ques
                            })

                        }} />
                </div>
                <div>

                    <div className="navi nav" >

                        <button disabled={!(this.props.index > 0)} onClick={() => {

                            if (this.props.index > 0) {
                                this.props.updateQuesArray(this.props.questions.map((e) => {
                                    if (e === this.props.questions[this.props.index]) {
                                        return this.state.ques;
                                    }
                                    else {
                                        return e;
                                    }
                                })
                                )
                                this.props.dec_index()
                            }
                        }}>
                            Previous   </button>
                        <button disabled={!(this.props.index < this.props.questions.length - 1)} onClick={() => {
                            if (this.props.index < this.props.questions.length - 1) {
                                if (this.state.ques.questionStatement.trim().length === 0) {
                                    alert('Enter Question Statement is ness');
                                    return;
                                }
                                if (this.state.ques.questionOptions[0].trim().length === 0 && this.state.ques.questionOptions[1].trim().length === 0) {
                                    alert('Enter atleast 2 options');
                                    return;
                                }
                                this.props.updateQuesArray(this.props.questions.map((e) => {
                                    if (e === this.props.questions[this.props.index]) {
                                        return this.state.ques;
                                    }
                                    else {
                                        return e;
                                    }
                                }));
                                this.props.inc_index()
                            }
                        }
                        }>
                            Next   </button>
                    </div>
                </div>
            </div >

        );
    }
}



