import React from 'react';
import firebase from 'firebase/app';
import { Redirect } from 'react-router-dom';
export class ResultTime extends React.Component {
    constructor(props) {
        super(props);
        let counter = 0;
        for (let question in this.props.questionBank) {
            if (this.props.questionBank[question].correctOptionIndex === this.props.responses[question]) {
                counter++;
            }
        }
        firebase.database().ref('users/' + this.props.quizLoad.username + "/" + this.props.quizLoad.code + "/submits/" + (this.props.user.email ? this.props.user.email.split("@")[0] : "waste")).once('value').then(
            (snapshot) => {
                if (snapshot.val()) {
                    this.setState({ result: snapshot.val().score })
                }
                else {
                    firebase.database().ref('users/' + this.props.quizLoad.username + "/" + this.props.quizLoad.code + "/submits/" + this.props.user.email.split("@")[0]).set({
                        username: this.props.user.email.split("@")[0],
                        time: new Date().getTime(),
                        responses: this.props.responses,
                        score: counter
                    })
                    console.log("OKKK")
                }
            }).catch(() => {
                console.log('OKK');
            })
        this.state = {
            result: counter
        }
    }

    render() {
        if (this.props.responses.length === 0) {
            return (
                <Redirect to="/giveQuiz" />
            )
        }
        return (
            <div style={{ display: 'flex', width: '100%', height: '100%', justifyContent: 'center', alignItems: 'center', backgroundColor: 'lightgreen', color: 'black', textShadow: '0px 0px 2px white', fontSize: '3em', fontWeight: '800' }}>
                <div>Your Score Is <br />
                    <br />
                    <span style={{ color: 'white', textShadow: '0px 0px 4px black' }}>{this.state.result}</span>
                </div>  </div>
        );
    }
}


