import React from 'react';
import firebase from 'firebase/app';
import { Link, Redirect } from "react-router-dom";
import { Input } from './Input';

import './login.css';
// import { PreUI } from './PreUI';


export class Register extends React.Component {
    constructor(props) {
        super(props);
        console.log(this.props);
        this.state = {
            user: '',
            password: '',
            err: {
                isTrue: false
            }
        }
    }
    render() {
        if (this.props.login === true) {
            return (<Redirect to="/" />);
        }


        return (
            <div className="mainStyle">
                <div>
                    <h2 style={{ margin: '1em auto' }}>Seems Like You Are Currently not Identified</h2>
                    <div className="register">
                        <div style={{ gridArea: 'x' }}>Plz SignUp First to Use This App....</div>
                        <Link to="/user/login" style={{ gridArea: 'button1' }} > <button className="button button3" onClick={() => {
                            this.setState({ doLogin: true, user: '', password: '' });
                        }}>
                            Login
                                </button></Link>
                        <label style={{ gridArea: 'label1', textAlign: 'left' }}>
                            Email Id :
                            </label>

                        <Input value={this.state.user} update={(e) => {
                            this.setState({
                                user: e.target.value

                            })
                        }} type="email" class="input input2" ga="input1" placeholder="Your Email-id" />

                        <label style={{ gridArea: 'label2', textAlign: 'left' }}>
                            Password:
                            </label>

                        <Input value={this.state.password} update={(e) => {
                            this.setState({

                                password: e.target.value
                            })
                        }} type="password" class="input input2" ga="input2" placeholder="Your Password" />


                        <button className="button button4" style={{
                            gridArea: 'button2',
                        }}
                            onClick={() => {
                                console.log(this.state);
                                firebase.auth().createUserWithEmailAndPassword(this.state.user, this.state.password).catch((error) => {
                                    // firebase.auth().createUserWithEmailAndPassword('manishekaneja@gmail.com', '123123').catch((error) => {
                                    this.setState({
                                        err:
                                            {
                                                isTrue: true,
                                                errorCode: error.code,
                                                errorMessage: error.message,
                                            }
                                        ,
                                        user: '',
                                        password: ''
                                    })
                                });
                            }}>
                            Register      </button>
                    </div>
                    <div style={this.state.err.isTrue === false ? { display: 'none' } : {
                        margin: '2em auto',
                        backgroundColor: 'darkred',
                        boxShadow: '2px 5px 2px white',
                        width: '75%',
                        padding: '0.5em'
                    }}>
                        Some Thing is Wrong. Try Once More....
                </div>
                </div >
            </div >
        );
    }
}



