export const reducer = (state = {
    user: {}, login: false, code: '', index: 0,
    quizLoad: {
        username: "",
        code: ""
    },
    responseArray: []
    , quizBlock: {
        details: {
            questionNo: 0,
            timeGiven: 0
        },
        questionBank: [
        ]
    },
    visible: true

}, action) => {
    switch (action.type) {
        case 'LOGIN':
            return {
                ...state,
                user: action.user,
                login: action.login
            }
        case 'LOADDATA':
            return {
                ...state,
                quizBlock: action.snapshot
            }
        case 'META_QUIZ':
            let qArr = [];
            for (let x = 0; x < action.details.questionNo; ++x) {
                qArr.push({
                    questionStatement: "",
                    questionOptions: ["", "", "", ""],
                    correctOptionIndex: -1
                })
            }
            return {
                ...state,
                quizBlock: {
                    details: {
                        questionNo: action.details.questionNo,
                        timeGiven: action.details.timeGiven
                    },
                    questionBank: qArr
                }
            }
        case 'INC_INDEX':
            return {
                ...state,
                index: state.index + 1
            }

        case 'DEC_INDEX':
            return {
                ...state,
                index: state.index - 1
            }
        case 'UPDATE_QUIZ':
            return {
                ...state,
                quizBlock: {
                    ...state.quizBlock,
                    questionBank: action.questionArray
                }
            }
        case 'LOAD_QUIZ':
            return {
                ...state,
                quizLoad: {
                    username: action.username,
                    code: action.code
                }

            }
        case 'TAKE_QUIZ':
            return {
                ...state,
                quizBlock: action.quizBlock

            }
        case 'UPDATE_RESPONSE':
            return {
                ...state,
                responseArray: action.array
            }
        case 'CHANGE_VISIBILITY':
            return {
                ...state,
                visible: action.value
            }
        default:
            return state;
    }
}


