import React from 'react';
import './login.css';
import { Input } from './Input';
import firebase from 'firebase/app';
import { Link, Redirect } from "react-router-dom";
import { FireBaseAuth } from './FireBaseAuth';
export class Login extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            user: '',
            password: '',
            err: {
                isTrue: false
            }
        }
    }

    render() {
        if (this.props.login === true) {
            return (<Redirect to="/" />);
        }

        return (
            <div className="mainStyle">
                <div>
                    <h2 style={{ margin: '1em auto' }}>Seems Like You Are Currently not Identified</h2>
                    <div className="login">
                        <div style={{ gridArea: 'x' }}>Perform the Nessasary Login Step to Continue....</div>
                        <Link to="/user/register" style={{ gridArea: 'button1' }} > <button className="button button1" onClick={() => { this.setState({ doLogin: false, user: '', password: '' }); }}>
                            Register
                            </button></Link>
                        <label style={{ gridArea: 'label1', textAlign: 'left' }}>
                            Email Id :
                            </label>
                        <Input value={this.state.user} update={(e) => {
                            this.setState({ user: e.target.value });
                        }} type="email" class="input input1" ga="input1" placeholder="Your Email-id" />
                        <label style={{ gridArea: 'label2', textAlign: 'left' }} >
                            Password:
                            </label>
                        <Input value={this.state.password} update={(e) => {
                            this.setState({ password: e.target.value });
                        }} type="password" class="input input1" ga="input2" placeholder="Your Password" />

                        <button className="button button2"
                            onClick={() => {
                                console.log(this.state);
                                firebase.auth().signInWithEmailAndPassword(this.state.user, this.state.password).catch((error) => {
                                    // firebase.auth().signInWithEmailAndPassword('manishekaneja@gmail.com', '123123').catch((error) => {
                                    this.setState({
                                        err:
                                            {
                                                isTrue: true,
                                                errorCode: error.code,
                                                errorMessage: error.message,
                                            },
                                        user: '',
                                        password: ''
                                    })
                                });
                            }}>
                            Login
                                </button>
                        <div style={{ gridColumn: '1/6' }}>
                            <FireBaseAuth uiConfig={{
                                callbacks: {
                                    signInSuccessWithAuthResult: (authResult, redirectUrl) => true,
                                    uiShown: () => { document.getElementById('loader').style.display = 'none'; }
                                },
                                signInFlow: 'popup',
                                signInSuccessUrl: '/',
                                signInOptions: [
                                    firebase.auth.GoogleAuthProvider.PROVIDER_ID,
                                    firebase.auth.FacebookAuthProvider.PROVIDER_ID
                                ],
                            }} firebaseAuth={firebase.auth()} />
                        </div>
                    </div>
                    <div style={this.state.err.isTrue === false ? { display: 'none' } : {
                        margin: '2em auto',
                        backgroundColor: 'darkred',
                        boxShadow: '2px 5px 2px white',
                        width: '75%',
                        padding: '0.5em'
                    }}>
                        Some Thing is Wrong. Try Once More....
                </div>
                </div>
            </div>
        );
    }
}




