import React from 'react';

export class Input extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            value: ""
        }
    }
    render() {
        return (
            <input type={this.props.type} className={this.props.class} style={{
                gridArea: this.props.ga
            }} value={this.props.value} placeholder={this.props.placeholder} onChange={this.props.update} />
        )
    }
}



