import React from 'react';
import './quesStyle.css';
export class QuestionBlock extends React.Component {
       render() {
        return (
            <div style={{ display: 'flex', flexDirection: 'column' }}>
                <h2 style={{ flex: 1, margin: '1em', fontSize: '3em' }}>Question      {(this.props.index + 1)}
                </h2>
                <textarea value={this.props.ques.questionStatement} className="questionStatement"
                    placeholder="Write your Question Here"
                    onChange={(e) => { this.props.updateQues({ ques: { ...this.props.ques, questionStatement: e.target.value } }) }}
                />
                <div style={{ flex: 4, overflowY: 'scroll', overflowX: 'hidden' }}>
                    {this.props.ques.questionOptions.map((ele, index) => {
                        return (
                            <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }} key={"op" + index}>
                                <input value={ele} style={{ flex: 80, margin: '1.5em 1em', height: '2em', }}
                                    placeholder={"Option " + (index + 1)}
                                    onChange={(e) => {
                                        this.props.updateQues({
                                            ques: {
                                                ...this.props.ques,
                                                questionOptions: this.props.ques.questionOptions.slice(0, index).concat([e.target.value]).concat(this.props.ques.questionOptions.slice(index + 1))
                                            }
                                        })
                                    }}
                                />
                                <div style={{ flex: 20 }}>
                                    <input type="checkbox" checked={index === this.props.ques.correctOptionIndex} onChange={(e) => {
                                        if (ele.trim().length === 0) {
                                            alert("Enter Option Value first");
                                            return;
                                        }
                                        this.props.updateQues({
                                            ques: {
                                                ...this.props.ques,
                                                correctOptionIndex: index
                                            }
                                        });
                                    }
                                    } /></div>
                            </div>)
                    })}
                </div>
            </div>
        );
    }
}




