import React from 'react';
export function G(props) {
    return (
        <span style={{ color: 'lightgreen' }}>
            {props.children}
        </span>
    )
}
export function R(props) {
    return (
        <span style={{ color: 'red' }}>
            {props.children}
        </span>
    )
}


