import React from 'react';
export class QuestionScreen extends React.Component {

    render() {
        console.log(this.props);
        return (
            <div style={{ flex: 2, display: 'flex', flexDirection: 'column', width: '100%', justifyContent: 'center', alignItems: 'center' }} >
                <div style={{ flex: 1, display: 'grid', margin: '20px', justifyContent: 'center', alignItems: 'center', gridTemplateColumns: '1fr', gridTemplateRows: '1fr', width: '100%', height: '100%', overflowY: 'scroll' }}>
                    <span style={{ fontSize: '2em' }}>
                        Q:{this.props.ques.questionStatement}
                    </span>
                </div>
                <div style={{ flex: 1, padding: '0.5em', display: 'grid', gridGap: '1em', gridAutoRows: '70px', gridTemplateColumns: '1fr 1fr', width: '100%', overflowY: 'scroll' }}>
                    {this.props.ques.questionOptions.map((ele, index) => {
                        return (
                            <div key={'o' + index}
                                onClick={() => {
                                    this.props.responseIs(index)
                                }}
                                style={{ boxShadow: '2px 2px 2px red', display: 'flex', textAlign: 'center', alignItems: 'center', backgroundColor: this.props.answers[this.props.i] === index ? 'orange' : 'transparent' }}><span style={{ flex: 1 }}>{index + 1}) {ele}</span></div>
                        )
                    })}

                </div>

            </div>

        );
    }
}


