export const LOGIN = (user, login) => ({
    type: 'LOGIN',
    user, login
})
export const LOADDATA = (data) => ({
    type: 'LOADDATA',
    snapshot: data
})
export const MEAT_QUIZ = (data) => ({
    type: 'META_QUIZ',
    details: {
        questionNo: data.questionNo,
        timeGiven: data.timeGiven
    }
})
export const LOAD_QUIZ = data => ({
    type: "LOAD_QUIZ",
    username: data.username,
    code: data.code
})
export const UPDATE_QUIZ = (data) => ({
    type: 'UPDATE_QUIZ',
    questionArray: data
})
export const TAKE_QUIZ = (data) => ({
    type: 'TAKE_QUIZ',
    quizBlock: data
})

export const INC_INDEX = () => ({
    type: 'INC_INDEX'
})
export const DEC_INDEX = () => ({
    type: 'DEC_INDEX'
})
export const UPDATE_RESPONSE = (data) => ({
    type: 'UPDATE_RESPONSE',
    array: data
})
export const CHANGE_VISIBILITY = (data) => ({
    type: 'CHANGE_VISIBILITY',
    value: data
})

