import React from 'react';
export function DarkBack(props) {
    return (
        <div style={{
            color: 'white',
            textAlign: 'center',
            backgroundColor: '#222',
            position: 'fixed',
            width: '100%',
            height: '100%',
            overflow:'hidden'
        }}>
            {props.children}
        </div>
    )
}



