import React from 'react';
import './quizDetails.css'
import { Link } from 'react-router-dom';
import { G } from '../Common/Color';

export default class QuizDetails extends React.Component {
    constructor() {
        super();
        this.state = {
            questionNo: 3,
            timeGiven: 0
        }
    }
    render() {
        return (
            <div className="flex_row">
                <div className="detGrid" style={{ overflow: 'scroll' }}>
                    <label className="labelStyle" style={{
                        gridArea: 'l1'
                    }}>
                        Total Number of Questions
                        </label>
                    <input className="custom_input" type="number" value={this.state.questionNo} style={{ gridArea: 'i1' }} placeholder="No. of Questions"
                        onChange={(e) => {
                            if (e.target.value >= 0)
                                this.setState({ questionNo: parseInt(e.target.value, 10) });
                        }} />
                    <label className="labelStyle" style={{
                        gridArea: 'l2',
                    }}>
                        Total Time for Test <G> (in min)</G>
                        <span style={{
                            display: 'block',
                            fontSize: '0.6em'
                        }}>(Zero for no Time Limit)</span>
                    </label>
                    <input className="custom_input" type="number" value={this.state.timeGiven} style={{
                        gridArea: 'i2'
                    }} placeholder="Time Here"
                        onChange={(e) => {
                            if (e.target.value >= 0)

                                this.setState({ timeGiven: parseInt(e.target.value, 10) });

                        }} />
                    <Link to='/create/enter' style={{ gridArea: 'b' }} className="button"
                        onClick={() => {
                            this.props.presentQuiz({ questionNo: this.state.questionNo, timeGiven: this.state.timeGiven });
                            if (!this.state.questionNo || this.state.questionNo === 0) {
                                alert("O questions not allowed");
                                return;
                            }
                            // firebase.database().ref('users/' + this.props.user.uid + "/quiz" + this.props.data.totalQuizes + "/details").set(this.state);
                            // let update = {}
                            // update['users/' + this.props.user.uid + "/totalQuizes"] = this.props.data.totalQuizes + 1 || 0;
                            // firebase.database().ref().update(update);

                        }}>
                        Create
                    </Link>
                </div>
            </div>
        )
    }
}
