import React from 'react';
import firebase from 'firebase/app';
import './flex_grid.css';
import { Redirect } from 'react-router-dom';

export class ResultList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            quiz: {},
            show: -1
        }

    }
    componentDidMount() {
        console.log(this.state)
        firebase.database().ref('users/' + (this.props.user.email ? this.props.user.email.split("@")[0] : "") + "/" + this.props.match.params.timestamp + "/submits/").on('value',
            (snapshot) => {
                if (snapshot.val()) {
                    this.setState({ quiz: snapshot.val() })
                }
            })
    }
    render() {
        if (!this.props.user.email) {
            return (<Redirect to="/" />)
        }
        return (
            <div className="flex" style={{ backgroundColor: 'blue', width: '100%', height: '100%', flexDirection: 'column', paddingTop: '4em' }}>
                <div className="flex" style={{ color: 'white', flexDirection: 'column', flex: 1 }}>
                    <div className="grid" style={{ gridGap: '1px', gridTemplateColumns: '1fr 1fr' }} >
                        <div className="flex" style={{ backgroundColor: 'red', height: '100%', width: '100%', boxShadow: '1px 1px darkred' }}>
                            Student Name
                        </div>
                        <div className="flex" style={{ backgroundColor: 'red', height: '100%', width: '100%', boxShadow: '1px 1px darkred' }}>
                            Score
                        </div>
                    </div>
                </div>
                <div className="flex" style={{ color: 'white', justifyContent: 'start', flexDirection: 'column', flex: 9, overflowY: 'scroll' }}>

                    {Object.keys(this.state.quiz).concat(Object.keys(this.state.quiz)).concat(Object.keys(this.state.quiz)).map((ele, index) => {
                        console.log(ele, index)
                        return (
                            <div key={"p" + index} className="grid" style={{ flex: 1, height: '100%', boxShadow: '0px 0px 1px white', margin: '1em', gridGap: '0.5em', gridTemplateColumns: '1fr 1fr' }} >
                                <div className="flex" style={{ fontSize: '1.5em', height: '50px' }}>
                                    {ele}
                                </div>
                                <div className="flex" style={{ fontSize: '1.5em', height: '50px' }}>
                                    {this.state.quiz[ele].score}
                                </div>
                                {this.state.show === index ?
                                    <div className="flex" style={{ gridColumn: '1/3', fontWeight: '700', height: '20px', flexDirection: 'column' }} >
                                        <div className="fullLength" onClick={() => { this.setState({ show: -1 }) }}> Hide Responses</div>
                                    </div>
                                    :
                                    <div className="flex" style={{ gridColumn: '1/3', fontWeight: '700', height: '20px' }}>
                                        <span onClick={() => { this.setState({ show: index }) }}>   Response List</span>
                                    </div>
                                }

                                {this.state.show === index ? <div className="flex" style={{ gridColumn: '1/3', fontWeight: '700', margin: '20px 2px', flexDirection: 'column' }} >

                                    <div className="fullLength">
                                        <table className="fullLength">
                                            <thead>
                                                <tr>
                                                    <th>
                                                        Question No
                </th>
                                                    <th>
                                                        Selected Response</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {this.state.quiz[ele].responses.map((ele, index) => {
                                                    return (
                                                        <tr key={index}>
                                                            <td>  Ques {index} </td><td> {ele}</td>
                                                        </tr>
                                                    );
                                                })}
                                            </tbody>

                                        </table>
                                    </div>
                                </div> : <div></div>}

                            </div>

                        )
                    })}
                </div ></div >
        );
    }
}


