import React from 'react';
// import * as firebase from 'firebase';
import { DarkBack } from './DarkBack';
import { Link } from 'react-router-dom';
export class Rules extends React.Component {
    constructor(props) {
        super(props);
        console.log(this.props)
    }
    componentDidMount(){
        this.props.changeTo(false);
    }
    render() {
        return (
            <DarkBack>
                <div style={{ width: '100%', display: 'flex', flexDirection: 'column', height: '100%' }}>
                    <div style={{
                        textTransform: 'uppercase',
                        height: '60px',
                        width: '100%',
                        zIndex: '1',
                        textAlign: 'center',
                        color: 'black',
                        textShadow: '1px 1px 5px lightgreen',
                        fontSize: '3em',
                        fontWeight: '700',
                        padding: '0.2em',
                        flex: 1,
                        backgroundColor: 'white'
                    }}>
                        Remember
                        </div>
                    <div style={{
                        padding: '0em 50px',
                        fontSize: '0.7em',
                        overflowY: 'scroll',
                        top: '60px',
                        width: '100%',
                        flex: 98
                    }}>
                        <ol style={{ textAlign: 'left' }}>
                            {this.props.rulesList.map((ele, index) => {
                                return (
                                    <li key={'rul' + index} style={{ textTransform: 'capitalize', fontSize: '2em' }}>{ele}</li>
                                )
                            })}
                        </ol>
                    </div>
                    <div style={{
                        width: '100%', backgroundColor: 'white'
                    }}>
                        <Link to={"/quizTime"}>
                            <button style={{
                                display: 'inline',
                                padding: '0.5em 1em',
                                backgroundColor: 'red',
                                border: '0px',
                                borderRadius: '1em',
                                color: 'white',
                                margin: '0.3em 0em',
                                fontSize: '1.5em',
                                flex: 1
                            }} id="stt" onClick={() => {
                                console.log('xyz');
                            }} >Start Test </button>
                        </Link>
                    </div>
                </div >
            </DarkBack >
        );

    }
}


