import React from 'react';
import { Link } from 'react-router-dom';
import './front.css';
export class Front extends React.Component {
    constructor() {
        super();
        let flag;
        if (localStorage) {
            flag = (localStorage.ask.toString() === 'true');
        } else {
            flag = true;
        }
        this.state = {
            ask: flag
        }
    };
    componentWillMount() {
        this.props.changeTo(true);
    }

    render() {
        return (
            <div style={{ width: '100%', height: '100%', position: 'absolute', display: 'flex', justifyContent: 'center', overflow: 'hidden' }}>
                <div className={"ani mygrid " + (this.state.ask === false ? "show" : "hide")} style={{ backgroundColor: '#52f', padding: '8em 1em' }} >
                    <Link to="/create" className={"full1 red"} > Create New Quiz </Link>
                    <Link to="/view" className={"full1 red"} > View Results </Link></div>
                <div className={"ani mygrid " + (this.state.ask === true ? "show" : "hide")} style={{ backgroundColor: '#f45', padding: '8em 1em' }} ><Link to="/giveQuiz" className={"full1 blue"} >Give A Quiz</Link></div>
                <button className="swap" onClick={() => {
                    if (localStorage) {
                        localStorage.ask = !this.state.ask;
                    }
                    this.setState({ ask: !this.state.ask });
                }}>Want to Ask?? </button>
            </div >
        );
    }
}




