import React from 'react';
import firebase from 'firebase/app';
import { Link, Redirect } from 'react-router-dom';
export class SelectQuiz extends React.Component {
    constructor(props) {
        super(props);
        // console.log(this.props)
        this.state = {
            data: {},
            value: ""
        }
    }
    componentWillMount() {
        if (this.props.user.email) {
            firebase.database().ref('users/' + this.props.user.email.split('@')[0]).on('value', (snapshot) => {
                if (snapshot.val()) {
                    this.setState({
                        data: snapshot.val(), value: Object.keys(snapshot.val())[0]
                    });
                }
                console.log(this.state)
            });
        }
    }
    render() {
        if (!this.props.user.email) {
            return (<Redirect to="/" />)
        }
        else {


            return (

                <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100%', width: '100%' }}>
                    <div style={{ color: 'white', fontWeight: '900', width: '100%', boxShadow: '0px 0px 2px white', padding: '2em' }}>
                        <div style={{ margin: '1em auto', fontSize: '2em' }}> Select Quiz </div>
                        <select onChange={(e) => { this.setState({ value: e.target.value }) }} style={{ minWidth: '50%', height: '3em', color: 'white', fontWeight: '900', backgroundColor: 'transparent', border: '0px transparent' }}>
                            {Object.keys(this.state.data).map((ele, index) => {
                                return (
                                    <option key={"o" + index} style={{ textAlign: 'center', justifyContent: 'center', color: 'black' }} value={ele}>{new Date(parseInt(ele, 10)).toLocaleString()} ({ele})</option>
                                );
                            })}

                        </select>
                        <div style={{ margin: '1em auto', color: 'white', fontWeight: '900', width: '100%' }}>
                            <Link to={"/view/" + this.state.value} style={{ backgroundColor: 'transparent', color: 'white', border: '0px solid transparent', fontWeight: '900', fontSize: '2em' }}>
                                GO
                            </Link>
                        </div>
                    </div>
                </div >
            );
        }
    }
}


