import React from 'react';
import {ui} from '../../App';
export class FireBaseAuth extends React.Component {
    componentDidMount() {
       ui.start('#firebaseui-auth-container', this.props.uiConfig);
    }
    render() {
        return (
            <div>
                <div id="firebaseui-auth-container"></div>
                <div id="loader">Loading...</div>
            </div >
        );
    }
}


