import React, { Component } from 'react';
import { LOGIN, MEAT_QUIZ, UPDATE_QUIZ, TAKE_QUIZ, INC_INDEX, DEC_INDEX, LOAD_QUIZ, UPDATE_RESPONSE, CHANGE_VISIBILITY } from "./store/actions";
import { connect } from 'react-redux';
import './main.css';
import firebaseui from 'firebaseui';
import { CONFIG } from "./store/actions";
import { Loader } from './components/Loader/Loader';
import { Front } from './components/firstScreen/Front';
import { BrowserRouter as Router, Switch, Route, Link, Redirect } from 'react-router-dom';
import { WhichQuiz } from './components/giveQuiz/WhichQuiz';
import firebase from 'firebase/app';
import { ResultTime } from './components/giveQuiz/ResultTime';
import { Rules } from './components/Common/Rules';
import { QuizingScreen } from './components/giveQuiz/QuizingScreen';
import QuizDetails from './components/UploadQuiz/QuizDetails';
import Create from './components/UploadQuiz/Create';
import { DarkBack } from './components/Common/DarkBack';
import { Login } from './components/LogIn/Login';
import { SelectQuiz } from './components/View/SelectQuiz';
import { Register } from './components/LogIn/Register';
import { ResultList } from './components/View/ResultList';

const fApp = firebase.initializeApp(CONFIG());
const ui = new firebaseui.auth.AuthUI(firebase.auth());
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      logincheck: false,
    }
  }
  componentDidMount() {
    firebase.auth().onAuthStateChanged((user) => {
      this.setState({ logincheck: true });
      if (user) {
        this.props.doLogin({ user: user, login: true })
      } else {
        this.props.doLogin({ user: {}, login: false })
      }
    });
  }
  render() {
    return (
      <div>
        <LoaderScreen loginCheck={this.state.logincheck} />
        <Router>
          <DarkBack>
            <Link to='/user' className="logout" style={{ display: (this.props.visible && this.props.login) ? 'block' : 'none', textAlign: 'center', color: 'white', opacity: 1, zIndex: '10', backgroundColor: 'rgba(32,32,32,0.4)' }} onClick={() => {
              firebase.auth().signOut();
            }}> Sign Out</Link>
            {this.props.login === true ?
              <Switch>
                <Route exact path="/" component={() => { return (<Front changeTo={this.props.changeVisi} />); }} />
                <Route exact path="/create" component={() => { return (<QuizDetails presentQuiz={this.props.presentQuiz} />); }} />
                <Route path="/create/enter" component={() => { return (<Create user={this.props.user.email.split("@")[0]} quizBlock={this.props.quizBlock} questions={this.props.quizBlock.questionBank} updateQuesArray={this.props.updateQuesArray} index={this.props.index} inc_index={this.props.inc_index} dec_index={this.props.dec_index} />) }} />
                <Route exact path="/view" component={() => { return (<SelectQuiz user={this.props.user} />) }} />
                <Route path="/view/:timestamp" component={(props) => { return (<ResultList match={props.match} quizLoad={this.props.loadQ} user={this.props.user} />) }} />
                <Route path="/giveQuiz" component={() => { return (<WhichQuiz take_quiz={this.props.take_quiz} loadQuiz={this.props.loadQuiz} />) }} />
                <Route path="/rules" component={() => { return (<Rules changeTo={this.props.changeVisi} rulesList={['i am snape', 'And Star Lord chutiya hai', 'crucio', 'i am snape', 'And Star Lord chutiya hai', 'crucio', 'crucio', 'i am snape', 'And Star Lord chutiya hai', 'crucio', 'crucio', 'i am snape', 'And Star Lord chutiya hai', 'crucio', 'crucio', 'i am snape', 'And Star Lord chutiya hai', 'crucio', 'i am snape', 'And Star Lord chutiya hai', 'crucio']} />) }} />
                <Route path="/quizTime" component={() => { return (<QuizingScreen changeTo={this.props.changeVisi} updateTheResponseArray={this.props.updateResponse} quizBlock={this.props.quizBlock} />) }} />
                <Route path="/resultsTime" component={() => { return (<ResultTime responses={this.props.responses} quizLoad={this.props.loadQ} user={this.props.user} questionBank={this.props.quizBlock.questionBank} />) }} />
                <Route path="/user" component={() => { return (<Redirect to="/" />) }} />
                <Route path="/*" component={() => { return (<h2>Error</h2>) }} />
              </Switch>
              :
              <Switch>
                <Route exact path="/user" component={() => { return (<Redirect to="/user/login" />) }} />
                <Route path="/user/login" component={() => { return (<Login login={this.props.login} />); }} />
                <Route path="/user/register" component={() => { return (<Register login={this.props.login} />) }} />
                <Route exact path="/" component={() => { return (<Redirect to="/user" />) }} />
                <Route path="/*" component={() => { return (<Redirect to="/" />) }} />
              </Switch>}
          </DarkBack>
        </Router>
      </div >
    );
  }
}

function LoaderScreen(props) {
  return (
    <div className={"loader " + (props.loginCheck === true ? " hl " : "")}>
      <div>Loading</div>        <Loader /></div>
  )
}


export default connect((state) => ({
  login: state.login,
  user: state.user,
  index: state.index,
  quizBlock: state.quizBlock,
  loadQ: state.quizLoad,
  responses: state.responseArray,
  visible: state.visible

}),
  (dispatch) => ({
    doLogin: obj => dispatch(LOGIN(obj.user, obj.login)),
    presentQuiz: objMetaDetails => dispatch(MEAT_QUIZ(objMetaDetails)),
    updateQuesArray: cmpArray => dispatch(UPDATE_QUIZ(cmpArray)),
    inc_index: () => dispatch(INC_INDEX()),
    dec_index: () => dispatch(DEC_INDEX()),
    loadQuiz: obj => dispatch(LOAD_QUIZ(obj)),
    take_quiz: obj => dispatch(TAKE_QUIZ(obj)),
    updateResponse: arr => dispatch(UPDATE_RESPONSE(arr)),
    changeVisi: val => dispatch(CHANGE_VISIBILITY(val))
  }))(App);
export { ui, fApp };



