import React from 'react';
import { QuestionScreen } from './QuestionScreen';
import { Redirect, Link } from 'react-router-dom';
export class QuizingScreen extends React.Component {
    constructor(props) {
        super(props);
        let mark = []
        let resp = []
        for (let i = 0; i < this.props.quizBlock.questionBank.length; i++) {
            mark.push(false);
            resp.push(-1);
        }
        console.log(mark, resp);
        this.state = {
            index: 0,
            marked: mark,
            answers: resp
        }

    }
    componentDidMount() {
        this.props.changeTo(false);
    }

    render() {
        if (this.props.quizBlock.questionBank.length === 0) {
            return (<Redirect to='/giveQuiz' />);
        }
        console.log(this.props);
        return (
            <div style={{ display: 'flex', flexDirection: 'column', width: '100%', height: '100%', justifyContent: 'center', alignItems: 'center' }}>
                <QuestionScreen
                    i={this.state.index}
                    responseIs={(index) => {
                        this.setState({ answers: [...this.state.answers.slice(0, this.state.index), index, ...this.state.answers.slice(this.state.index + 1)] })
                    }}
                    answers={this.state.answers}
                    ques={this.props.quizBlock.questionBank[this.state.index]} />
                <div style={{ flex: 1, display: 'grid', gridTemplateRows: '2fr 7fr 1fr', gridGap: '1em', gridTemplateColumns: '1fr 1fr 1fr', width: '100%' }} >
                    <button onClick={(e) => {
                        if (this.state.index > 0)
                            this.setState({ index: this.state.index - 1 })
                    }}>
                        Prevoius
                        </button>
                    <button onClick={(e) => {
                        this.setState({ marked: [...this.state.marked.slice(0, this.state.index), !this.state.marked[this.state.index], ...this.state.marked.slice(this.state.index + 1)] })

                    }}>
                        Mark
                        </button>
                    <button onClick={(e) => {
                        if (this.state.index < this.props.quizBlock.questionBank.length - 1)
                            this.setState({ index: this.state.index + 1 })
                    }}>
                        Next
                        </button>
                    <div style={{ display: 'grid', gridTemplateColumns: 'repeat(10,1fr)', gridAutoRows: '40px', width: '100%', overflowY: 'scroll', gridColumn: "1/4" }}>
                        {this.state.marked.map((ele, index) => {
                            return (
                                <div key={index} onClick={() => {
                                    this.setState({ index: index })
                                }} style={{ backgroundColor: (ele === true ? 'green' : 'transparent') }}>{index + 1}</div>
                            )
                        })}
                    </div>
                    <Link to="/resultsTime" style={{ gridColumn: "1/4", color: 'white', display: 'flex', justifyContent: 'center', alignItems: 'center', padding: '0.5em' }} onClick={() => {
                        this.props.updateTheResponseArray(this.state.answers);
                    }} >
                        Finish Test
                    </Link>

                </div>
            </div >
        );
    }
}



